import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';

import { auth, createUser } from './firebase/firebase.utils';
import { setCurrentUser } from './redux/user/user.action';
import { selectShopCollectionToArray } from './redux/shop/shop.selector';

import NavigationBar from './components/navigation/navigation.component';
import HomePage from './pages/homepage/homepage.component';
import About from './components/about/about.component';
import Checkout from './components/checkout/checkout.component';
import ShopPageComponent from './pages/shop-page/shop-page.component';
import Footer from './components/footer/footer.component';

import './App.css';

class App extends Component {


  unsubscribeFromAuth = null;

  componentDidMount() {

    const { setCurrentUser } = this.props;

    this.unsubscribeFromAuth = auth.onAuthStateChanged(async userAuth => {
      if (userAuth) {

        const userRef = await createUser(userAuth);

        userRef.onSnapshot(snapshot => {
          setCurrentUser({
            id: snapshot.id,
            ...snapshot.data()
          })
        })
      }

      setCurrentUser(userAuth);
    })
  }

  componentWillUnmount() {
    this.unsubscribeFromAuth();
  }

  render() {
    return (
      <div className="App">
        <NavigationBar />
        <div className="container">
          <Switch>
            <Route exact path='/liza-shop-online/' component={HomePage} />
            <Route path='/liza-shop-online/shop' component={ShopPageComponent} />
            {/* <Route exact path='/liza-shop-online/shop' component={Collection} />
            <Route path='/liza-shop-online/shop/:collectionId' component={CollectionPage} /> */}
            <Route path='/liza-shop-online/about' component={About} />
            <Route path='/liza-shop-online/checkout' component={Checkout} />
          </Switch>
        </div>
        <Footer />
      </div>
    )
  }
}

const mapStateToProps = createStructuredSelector({
  collections: selectShopCollectionToArray
})

const mapDispatchToProps = dispatch => ({
  setCurrentUser: userAuth => dispatch(setCurrentUser(userAuth))
})

export default connect(mapStateToProps, mapDispatchToProps)(App);
