export const mapCollection = collection => {
    const transformedCollection = collection.docs.map(docs => {
        const { title, items } = docs.data();

        return {
            routeName: encodeURI(`shop/${title.toLowerCase()}`),
            title,
            items
        }
    })

    return transformedCollection.reduce((accumulator, currentItem) => {
        accumulator[currentItem.title.toLowerCase()] = currentItem;
        return accumulator;
    }, {});
}