import React from 'react';
import { Route, Switch } from 'react-router-dom';
import { connect } from 'react-redux';

import { firestore } from '../../firebase/firebase.utils';
import { mapCollection } from '../../redux/shop/shop.utils';

import { addShopDataToState } from '../../redux/shop/shop.action';

import WithSpinner from '../../components/with-spinner/with-spinner.component';
import Collection from '../../components/collection/collection.component';
import CollectionPage from '../../components/collection-page/collection-page.component';

import './shop-page.styles.scss';

const WithSpinnerCollection = WithSpinner(Collection);
const WithSpinnerCllectionPage = WithSpinner(CollectionPage);

class ShopPageComponent extends React.Component {

    state = {
        isLoading: true
    }

    unsubscribeFromShop = null;

    componentDidMount() {
        const { addCollections } = this.props;

        const collectionRef = firestore.collection('collections');

        collectionRef.onSnapshot(async snapshot => {
            addCollections(mapCollection(snapshot))
            this.setState({ isLoading: false })
        })

    }

    render() {
        const { match } = this.props;
        return (
            <div className="shop-page-component">
                <Switch>

                    <Route exact path={`${match.path}`} render={(props) => <WithSpinnerCollection isLoading={this.state.isLoading} {...props} />} />
                    <Route exact path={`${match.path}/:collectionId`} render={(props) => <WithSpinnerCllectionPage isLoading={this.state.isLoading} {...props} />} />
                </Switch>
            </div>
        )
    }
}

const mapDispatchToProps = dispatch => ({
    addCollections: collections => dispatch(addShopDataToState(collections))
})

export default connect(null, mapDispatchToProps)(ShopPageComponent);