import React from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';

import { clearCartItems } from '../../redux/cart-items/cart-items.actions';

import StripeCheckout from 'react-stripe-checkout';

const StripeCheckoutButton = ({ total, history, clearCartItems }) => {
    const convertedPrice = total * 100;
    const stripeKey = 'pk_test_DedhFi1KRb91z1YXQ4Okaq2Q00lRNusH6E';

    const token = (token) => {
        console.log(token);
        alert("Payment Successfull");
        history.push('/liza-shop-online/');
        clearCartItems();
    }

    return (
        <StripeCheckout
            name="Li-Shop Online Selling"
            email="li-shop.98labs.io"
            token={(token)}
            amount={convertedPrice}
            panelLabel={`Pay`}
            stripeKey={stripeKey}
        />
    )
}

const mapDispatchToProps = dispatch => ({
    clearCartItems: () => dispatch(clearCartItems())
})

export default withRouter(connect(null, mapDispatchToProps)(StripeCheckoutButton));